
(function() {

  define('coincs/main',['utils', 'coincs/hive', 'jquery', 'jquery-ui'], function(utils, hive, $, _) {
    var group;
    console.log("Starting");
    group = utils.definitions.group;
    return $(function() {
      var hivePlot;
      hivePlot = new hive.HivePlot("#hive", group);
      hivePlot.snrBaseThreshold(8);
      hivePlot.title(group.name);
      return hivePlot.load();
    });
  });

}).call(this);
